import React from "react";
import { DataGrid } from "@mui/x-data-grid";
import { Button, Typography, Box } from "@mui/material";

const PostList = ({ posts, onDeletePost }) => {
  // Data Grid  columns 
  const columns = [
    {
      field: "title",
      headerName: "Title",
      flex: 1, //   
      renderHeader: () => (
        <Typography variant="h6" style={{ fontWeight: "bold" }}>
          Title
        </Typography>
      ),
    },
    {
      field: "content",
      headerName: "Content",
      flex: 2,
      renderHeader: () => (
        <Typography variant="h6" style={{ fontWeight: "bold" }}>
          Content
        </Typography>
      ),
    },
    {
      field: "actions",
      headerName: "Actions",
      flex: 1,
      renderCell: (params) => (
        <Button
          color="secondary"
          variant="contained"
          onClick={() => onDeletePost(params.row.id)}
        >
          Delete
        </Button>
      ),
      sortable: false, //  
      filterable: false, //  
    },
  ];

  return (
    <Box sx={{ height: 400, width: "100%" }}>
      <DataGrid
        rows={posts} // rows  
        columns={columns} // columns  
        pageSize={5} //   
        rowsPerPageOptions={[5, 10, 20]} //    
        disableSelectionOnClick //     
        autoHeight //      
      />
    </Box>
  );
};

export default PostList;
